import { DownOutlined } from '@ant-design/icons';
import { Button, Col, Divider, Dropdown, Menu, Row } from 'antd';
import React from 'react';
import { Link } from 'react-router-dom';
import './index.less';


export default class Header extends React.Component {
    state = {
        userName: null,
    };

    componentWillMount() {
        this.setState({
            userName: '深海如梦'
        });
    }

    exit = () => {
        window.location.href = './#/'
    }

    render() {
        const menu = (
            <Menu>
                <Menu.Item key="0">
                    <a onClick={this.exit}>退出</a>
                </Menu.Item>
            </Menu>
        )
        return (
            <div className="header">
                <Row className="header-top">
                    <Col span={10} className="logo">
                        <Link to="/management"><span>项目管理</span></Link>
                    </Col>
                    <Col span={14}>
                        <Dropdown overlay={menu} trigger={['click']}>
                            <Button type="link" style={{ color: '#ffffff' }}>{this.state.userName} <DownOutlined /></Button>
                        </Dropdown>
                    </Col>
                </Row>
            </div>
        );
    }
}