import React from 'react'
import { HashRouter, Redirect, Route, Switch } from 'react-router-dom'
import App from './App';
import Admin from './admin';
import Login from './pages/login';
import Home from './pages/home';
import Projects from './pages/projects';
import webKnow from './pages/webKnow';
import Test from './pages/test';

export default class IRouter extends React.Component {

    render() {
        return (
            <HashRouter>
                <App>
                    <Switch> {/* 加switch只会访问一个 */}
                        {/* 登录 */}
                        <Route path="/" exact component={Login}></Route>
                        {/* 主页面 */}
                        <Route path="/management" render={() =>
                            <Admin>
                                <Switch>
                                    <Route path="/management/home" component={Home}></Route>
                                    {/* 项目列表 */}
                                    <Route path="/management/projects" component={Projects} />

                                    {/* 获取前端知识文章（掘金渠道） */}
                                    <Route path="/management/webKnow" component={webKnow} />

                                    <Route path="/management/test" component={Test} />

                                    {/* 重定向 */}
                                    <Redirect to="/management/home" />

                                </Switch>
                            </Admin>
                        } />
                    </Switch>
                </App>
            </HashRouter>
        );
    }
}
