import { Button, Layout, Spin } from 'antd';
import React, { Fragment } from 'react';
import './index.less'

const { Content } = Layout;

interface Props {
    history?: any
}


export default class Login extends React.Component<Props> {

    state = {
        isShow: false,
    }

    enterSys = () => {
        this.props.history.push({ pathname: '/management' });
    }

    componentDidMount() {
        this.setState({
            isShow: true
        })
        setTimeout(() => {
            this.setState({
                isShow: false
            })
        }, 2000);
    }

    render() {
        const { isShow } = this.state
        return (
            <Layout className="site-layout" style={{ position: 'relative', overflow: 'hidden', height: '100vh', background: '#ffffff' }}>
                <Content style={{ margin: '0px 18px 0', overflow: 'hidden' }}>
                    <Spin tip="加载中..." spinning={isShow} style={{ marginTop: '50vh', fontSize: 18 }} size="large">
                        {
                            !isShow ? <div style={{ textAlign: 'center', marginTop: '35vh' }}>
                                <img style={{ marginBottom: 20, borderRadius: '10%', width: 150 }} src="https://willern.gitee.io/img/blog.jpg" />
                                <h2>欢迎使用 系统</h2>
                                <Button type="primary" style={{ marginTop: 10 }} onClick={this.enterSys}>进入系统</Button>
                            </div> : null
                        }
                    </Spin>
                </Content>
            </Layout>
        );
    }
}