
import { Avatar, Button, Card, Col, List, message, Row, Skeleton, Tag, Tooltip } from 'antd';
import axios from 'axios'
import moment from 'moment';
import React, { Component } from 'react'
import { EditOutlined, EllipsisOutlined, SettingOutlined, HomeOutlined } from '@ant-design/icons';
import './index.less'

export default class Home extends React.Component {

    state = {
        initLoading: true,
        loading: false,
        knowInitData: [],
        data: [],
        list: [],
        count: 1,
        pageIndex: 8,
        pLoading: false,
        zList: [],
    }

    componentDidMount() {
        this.knowInit();
        this.request(res => {
            let data = [];
            if (res.status === 200 && res.data.success) {
                data = res.data.data
            } else {
                data = [];
            }
            setTimeout(() => {
                this.setState({
                    initLoading: false,
                    list: data,
                    data,
                });
            }, 1000);

        });
        this.setState({
            pLoading: true
        })
        setTimeout(() => {
            this.setState({
                pLoading: false
            })
        }, 1000);
        this.request2();
    }

    /**
     * 获取知识
     * @param callback 
     */
    request = callback => {
        const { pageIndex, count } = this.state
        const params = {
            params: {
                page: count, //页数
                limit: pageIndex, //每一页的主题数量
                tab: 'all', //String 主题分类。目前有 ask share job good all
                mdrender: false //当为 false 时，不渲染。默认为 true，渲染出现的所有 markdown 格式文本。
            }
        }
        axios.get('https://cnodejs.org/api/v1/topics', params).then(res => {
            // console.log(res);
            callback(res);
        })
    };

    /**
     * 知识获取更多
     */
    onLoadMore = () => {
        // console.log(this.state.count);
        let count = ++this.state.count;
        this.setState({
            loading: true,
            count: count,
        });

        this.request(res => {
            let data = [];
            if (res.status === 200 && res.data.success) {
                data = res.data.data.map(item => {
                    item.loading = true
                    return item
                })
            } else {
                data = [];
            }
            const result = this.state.data.concat(data);
            // console.log(result);
            this.setState({
                loading: false,
                list: result,
                data: result,
            })
            let root = document.getElementById('know');
            if (root) root.scrollTop = 99999999
            setTimeout(() => {
                this.setState(
                    {
                        list: result.map(item => {
                            item.loading = false
                            return item
                        }),
                        count: count++,
                    },
                    () => {
                        // Resetting window's offsetTop so as to display react-virtualized demo underfloor.
                        // In real scene, you can using public method of react-virtualized:
                        // https://stackoverflow.com/questions/46700726/how-to-use-public-method-updateposition-of-react-virtualized
                        window.dispatchEvent(new Event('resize'));
                    },
                );
                let root = document.getElementById('know');
                if (root) root.scrollTop = 99999999
            }, 1000);
        });
    }

    request2 = async () => {

        /* 设置了跨域请求，在package.json中  "proxy": "https://www.zhihu.com" */
        await axios.get('/api/v4/search/top_search').then(res => {
            if (res.status === 200) {
                console.log(res);
                const data = res.data.top_search.words
                this.setState({
                    zList: data
                })
            } else {
                message.warn(res.statusText);
            }

        })
    }

    /**
     * 前端知识初始化骨架屏
     */
    knowInit = () => {
        const data = []
        for (let index = 0; index < 10; index++) {
            data.push(<Skeleton key={index} title={false} avatar active />)
        }
        this.setState({
            knowInitData: data
        })
    }


    render() {
        const { initLoading, loading, list, pLoading } = this.state
        const loadMore =
            !initLoading && !loading ? (
                <div
                    style={{
                        textAlign: 'center',
                        marginTop: 12,
                        height: 32,
                        lineHeight: '32px',
                    }}
                >
                    <Button onClick={this.onLoadMore}>加载更多</Button>
                </div>
            ) : null;
        const imgs = [
            { title: '9lala游戏', desc: '9lala游戏', link: '//evanjason.github.io/9lala/' },
            { title: 'Lcar管理系统', desc: 'Lcar管理系统', link: '//evanjason.github.io/lcarSystem/#/login' },
            { title: 'react个人博客', desc: 'react个人博客', link: '//evanjason.github.io/' },
        ]
        const imgList = imgs.map((item, index) => {
            return (
                <Skeleton active loading={pLoading}>
                    <Card
                        hoverable
                        style={{ marginBottom: 10 }}
                        // cover={<img style={{ height: 150 }} src="https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png" />}
                        actions={[
                            <Tooltip title={`点击进入${item.title}`}><HomeOutlined key="enter" onClick={() => { window.open(item.link) }} /></Tooltip>,
                            <SettingOutlined key="setting" />,
                            <EditOutlined key="edit" />,
                            <EllipsisOutlined key="ellipsis" />,
                        ]}

                    >
                        <Card.Meta
                            title={item.title}
                            description={item.desc}
                        />
                    </Card>
                </Skeleton>
            )
        })
        return (
            <div>
                <div className="home-wrap">
                    <Row gutter={16}>
                        {/* 前端知识 */}
                        <Col id="know" className="section" span={12} style={{ maxHeight: 'calc(80vh)', overflow: !initLoading ? 'auto' : null }}>
                            <Card title="前端知识" bordered={false} style={{ minHeight: 'calc(80vh)', }}>
                                {
                                    initLoading ? this.state.knowInitData.map((item) => {
                                        return item;
                                    }) : <List
                                        style={{ marginTop: 20 }}
                                        loading={initLoading}
                                        itemLayout="horizontal"
                                        loadMore={loadMore}
                                        dataSource={list}
                                        renderItem={(item, index) => (
                                            index > 3 ?
                                                <List.Item>
                                                    <Skeleton title={false} loading={item.loading} avatar active>
                                                        <List.Item.Meta
                                                            avatar={
                                                                <Avatar src={item.author.avatar_url} />
                                                            }
                                                            title={
                                                                <Row>
                                                                    <Col span={18}>{item.title}</Col>
                                                                    <Col span={6} style={{ textAlign: 'right' }}>创建时间：{moment(item.create_at).format('YYYY-MM-DD')}</Col>
                                                                </Row>
                                                            }
                                                            description={
                                                                <Row>
                                                                    <Col span={18}><a href={'https://cnodejs.org/topic/' + item.id} target="_blank">{item.content.length < 100 ? item.content : item.content.substring(0, 150).concat(' ...')}</a></Col>
                                                                    <Col span={6} style={{ textAlign: 'right' }}>最新回复：{moment(item.last_reply_at).format('YYYY-MM-DD')}</Col>
                                                                </Row>
                                                            }
                                                        />
                                                    </Skeleton>
                                                </List.Item> : null
                                        )}
                                    />
                                }

                            </Card>
                        </Col>
                        {/* 项目 */}
                        <Col className="section" span={12}>
                            <Card title="项目列表" bordered={false} style={{ minHeight: 'calc(80vh)', }}>
                                <Row gutter={16}>
                                    {
                                        imgList.map((item, index) => {
                                            return (
                                                <Col span={8} key={index}>
                                                    {item}
                                                </Col>
                                            )
                                        })
                                    }
                                </Row>
                            </Card>
                        </Col>
                    </Row>
                    <Card style={{ marginTop: 20 }}>
                        {
                            process.env.NODE_ENV !== 'production' ?
                                <Card title="知乎热搜" bordered={false}>
                                    {
                                        initLoading ? this.state.knowInitData.map((item) => {
                                            return item
                                                ;
                                        }) : <List
                                            size="small"
                                            dataSource={this.state.zList}
                                            renderItem={(item, index) => <Skeleton active loading={pLoading}>
                                                <List.Item><Tag color={(index) < 3 ? '#f50' : '#108ee9'} style={{ width: 30, textAlign: 'center' }}>{index + 1}</Tag>{item.display_query}</List.Item>
                                            </Skeleton>}
                                        />
                                    }

                                </Card> : null
                        }
                    </Card>
                </div>
            </div>
        );
    }
}