import { Card, List, message } from 'antd';
import axios from 'axios';
import React, { Component, Fragment } from 'react'
class Test extends Component {


    state = {
        list: [],
    }

    componentDidMount() {
        this.request();
    }

    request = async () => {

        /* 设置了跨域请求，在package.json中  "proxy": "https://www.zhihu.com" */
        await axios.get('/api/v4/search/top_search').then(res => {
            if (res.status === 200) {
                console.log(res);
                const data = res.data.top_search.words
                this.setState({
                    list: data
                })
            } else {
                message.warn(res.statusText);
            }

        })

    }

    render() {
        return (<Fragment>
            <Card>
                <List
                    size="small"
                    bordered
                    dataSource={this.state.list}
                    renderItem={item => <List.Item>{item.display_query}</List.Item>}
                />
            </Card>
        </Fragment>)
    }
}

export default Test